.board-container {
  --grid-size: 50px;
  position: relative;
  background-color: #e6ceac;
  padding: calc(var(--grid-size) / 2);
  border: 4px solid #5d4037;
  border-radius: 4px;
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.5);
}

.board-lines {
  position: relative;
  width: calc(var(--grid-size) * 8);
  height: calc(var(--grid-size) * 9);
  border: 1px solid #333;
}

.line-v {
  position: absolute;
  width: 1px;
  background-color: #333;
  top: 0;
  bottom: 0;
}

.line-h {
  position: absolute;
  height: 1px;
  background-color: #333;
  left: 0;
  right: 0;
}

.river-bank {
  position: absolute;
  top: calc(var(--grid-size) * 4);
  height: var(--grid-size);
  left: 0;
  right: 0;
  background-color: #e6ceac;
  /* Overlays the middle vertical lines */
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: serif;
  font-weight: bold;
  font-size: 1.5rem;
  color: #5d4037;
  z-index: 1;
}

.palace-x {
  position: absolute;
  width: calc(var(--grid-size) * 2);
  height: calc(var(--grid-size) * 2);
  pointer-events: none;
}

.palace-x::before,
.palace-x::after {
  content: '';
  position: absolute;
  background-color: #333;
  width: 100%;
  height: 1px;
  top: 50%;
  left: 0;
}

.palace-x::before {
  transform: rotate(45deg);
}

.palace-x::after {
  transform: rotate(-45deg);
}

.pieces-layer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.piece-slot {
  position: absolute;
  width: var(--grid-size);
  height: var(--grid-size);
  transform: translate(-50%, -50%);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 10;
}

.piece-inner {
  width: 85%;
  height: 85%;
  border-radius: 50%;
  background-color: #f9f4e8;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  font-size: 1.5rem;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3), inset 0 0 10px rgba(0, 0, 0, 0.1);
  border: 2px solid #333;
  transition: all 0.2s;
}

.piece-inner.red {
  color: #cc0000;
  border-color: #cc0000;
}

.piece-inner.black {
  color: #1a1a1a;
  border-color: #1a1a1a;
}

.piece-slot.selected .piece-inner {
  transform: scale(1.15);
  box-shadow: 0 0 15px #ffd700, 0 8px 16px rgba(0, 0, 0, 0.4);
  border-color: #ffd700;
  z-index: 20;
}

.move-hint {
  width: 12px;
  height: 12px;
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 50%;
}

.capture-hint {
  width: 80%;
  height: 80%;
  border: 2px dashed rgba(255, 0, 0, 0.4);
  border-radius: 50%;
}